.Button {
    display: inline-block;

    box-sizing: border-box;
    padding: 10px;

    cursor: pointer;
    text-align: center;
    border: 1px solid gray;
    border-radius: 5px;

    font-size: 14px;
    transition: all 0.2s;

    &:focus {
        border: 1px solid black
    }
}
